.menu-button {
  position: relative;
  display: block;
  padding: 0;
  border: none;
  background-color: @orange-500;
  width: 50px;
  height: 50px;
  z-index: 3;

  &::before {
    content: "";
    width: 22px;
    height: 1px;
    background-color: @white;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    box-shadow: 0 5px 0 0 @basic-white, 0 -5px 0 0 @basic-white;
  }

  @media (min-width: 660px) {
    margin-top: 43px;
  }

  @media (min-width: 1200px) {
    display: none;
  }
}
