.nav {
  font-size: 20px;
  line-height: 28px;
  font-weight: 600;
  display: none;
  width: 100%;
  padding-top: 134px;
  padding-bottom: 50px;
  min-height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  background-color: @white;

  @media (min-width: 1200px) {
    display: block;
    max-width: 620px;
    line-height: 18px;
    font-weight: normal;
    text-transform: initial;
  }
}


.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;

  @media (min-width: 1200px) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    background: transparent;
    border: none;
  }
}

.nav__item {
  padding: 0 15px;
  background-color: @white-500;

  &:not(:last-child) {
    margin-bottom: 30px;
  }

  a {
    color: @black-300;
    display: block;
    text-decoration: none;
    padding-top: 11px;
    padding-bottom: 11px;

    &:hover {
      color: @basic-red;
    }

    &:active {
      opacity: 0.3;
    }
  }

  &--current {
    @media (min-width: 1200px) {
      border-bottom: 2px solid @basic-white;

      a:hover,
      a:active {
        color: @basic-white;
        opacity: 1;
      }
    }
  }

  @media (min-width: 1200px) {
    box-shadow: none;
    padding: 0;

    a:active {
      color: @basic-white;
      opacity: 0.3;
    }
  }
}
