.header {
  background-color: @white;

  &--no-js {
    position: static;

    .header__nav {
      display: block;
      position: static;
      padding-top: 50px;
      padding-bottom: 50px;
      min-height: auto;
    }

    .header__menu-button {
      display: none;
    }
  }

  &--nav-open {
    .header__nav {
      display: block;
    }
  }
}

.header__flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  min-height: 50px;
  align-items: center;

  @media (min-width: 660px) {
    min-height: 66px;
  }

  @media (min-width: 1200px) {
    max-width: 940px;
    margin: 0 auto;
  }
}
